<script>
  import { elasticOut } from "svelte/easing";

  // Contains an array of Javascript objects.
  // Each object contains: 
  // [
  //    {
  //         className: string
  //         image: string (url to image)
  //         members: [{
  //           name: string
  //           major: string
  //           pixelLocationX: number (pixel location on class picture)
  //           pixelLocationY: number (same but on y axis)
  //           linkedIn: string (the url)
  //         },
  //     ...]
  //    },
  // ...]
  // basically an ar
  // order from newest to oldest
  export let pledgeClasses;

  let visible = true;

  let classIndex = 0;
  let curClass = pledgeClasses[classIndex];

  let my, mx;

  function handleMousemove(event) {
		mx = event.clientX;
		my = event.clientY;
	}
</script>

<style>
  .cursor-box {
    position: absolute;
    padding: 2em;
    border: 1em gray dotted;
  }
</style>

<p>mx:{mx}</p>
<p>my:{my}</p>

<div 
  class="cursor-box"
  style="
    top: {mx};
    left: {my};
  "
>
</div>

<div class="class-img-container">
  <img
    on:mousemove={handleMousemove}
    class="class-img"
    src="{curClass.image}"
    alt="{curClass.className}"
  >
</div>